import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { P2pchatComponent } from './p2pchat.component';

const routes: Routes = [{ path: '', component: P2pchatComponent }];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class P2pchatRoutingModule { }
